    padding-top: 40px;
  }

  .news_photo_block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 30px;
    padding-top: 20px;
    padding-left: 15px;
    padding-right: 15px;
  }

  .similar_news_photo_block:hover .hover_news_video {
    margin-top: -110px;
    margin-bottom: 37px;
  }

  .grey_block_under_news_photo p {
    font: 10px $font-article;
    font-weight: bolder;
    padding: 8px 13px;
  }

  .similar_news_photo_block h6 {
    font: 15px $font-article;
    font-weight: bolder;
  }

  .text_to_ten_block p:first-child {
    font: 8px $font-basic;
  }

  .text_to_ten_block p:last-child {
    padding-top: 20px;
    font: 11px $font-basic;
  }

  .link_ten_block {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}

